.wrapper {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #FFF;
}
.wrapper_tip {
  width: 90%;
  min-height: 40px;
  margin: 30px auto 0;
}
.wrapper_tip_container {
  display: flex;
  align-items: center;
}
.wrapper_tip_container_avator {
  width: 40px;
  height: 40px;
  background-color: #F7F7F7;
  border-radius: 50%;
}
.wrapper_tip_container_avator > img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.wrapper_tip_container_name {
  margin: 0 10px;
}
@media screen and (max-width: 321px) {
  .wrapper_tip_container_name {
    /*小*/
    font-size: 18px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_tip_container_name {
    /*中*/
    font-size: 19px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_tip_container_name {
    /*大*/
    font-size: 20px;
  }
}
.wrapper_tip_container_extra {
  color: #AEAEAE;
}
@media screen and (max-width: 321px) {
  .wrapper_tip_container_extra {
    /*小*/
    font-size: 14px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_tip_container_extra {
    /*中*/
    font-size: 15px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_tip_container_extra {
    /*大*/
    font-size: 17px;
  }
}
.wrapper_content {
  width: 80%;
  margin: 70px auto 0;
}
.wrapper_content_list :global(.am-list-body) > div:first-child {
  margin-bottom: 15px;
}
.wrapper_content_list :global(.am-list-body) :global(.iconfont) {
  font-size: 24px;
}
.wrapper_content_button {
  margin: 20px 0 10px 0;
}
.wrapper_content_extra {
  color: #FF0000;
}
@media screen and (max-width: 321px) {
  .wrapper_content_extra {
    /*小*/
    font-size: 12px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_content_extra {
    /*中*/
    font-size: 13px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_content_extra {
    /*大*/
    font-size: 15px;
  }
}
